@color_1: #333;
@color_2: #4682be;
@color_3: #fff;
@color_4: #a6e22e;
@color_5: #c7254e;
@color_6: #ed1c24;
@color_7: #2980b9;
@color_8: #75715e;
@color_9: #f8f8f2;
@color_10: #f92672;
@color_11: #ae81ff;
@color_12: #e6db74;
@color_13: #66d9ef;
@color_14: #fd971f;
@font_family_1: Verdana, Arial;
@font_family_2: Consolas,'Courier New',Courier,FreeMono,monospace;
@font_family_3: Consolas, Monaco, 'Andale Mono', monospace;
@background_color_1: #fff;
@background_color_2: #f8f8f8;
@background_color_3: #aaa;
@background_color_4: #444;
@background_color_5: #409eff;

html
{
    font-size: 100%;
}
body
{
    font-family: @font_family_1;
    font-size: .8rem;

    margin: 0;
    padding: 0;

    color: @color_1;
}
#sidebar
{
    margin-top: 0;
    padding-top: 25px;
    padding-bottom: 25px;
    padding-left: 25px;

    box-shadow: 0 0 20px #ccc;
    h1
    {
        font-size: 25px;

        margin-bottom: 0;
        padding-bottom: 0;
        a
        {
            &:link
            {
                color: @color_1;
            }
            &:visited
            {
                color: @color_1;
            }
        }
    }
    h2
    {
        font-size: .7rem;
    }
    h5
    {
        margin-top: 20px;
        margin-bottom: 0;
    }
    a
    {
        &:visited
        {
            text-decoration: none;

            color: @color_2;
        }
        &:link
        {
            text-decoration: none;

            color: @color_2;
        }
    }
    ul
    {
        margin: 0;
        padding-top: 0;
        padding-left: 10px;

        list-style-type: none;
        li
        {
            font-size: .7rem;

            margin-top: 0;
            margin-bottom: .2rem;
            margin-left: 10px;
            padding: 0;

            text-indent: -5px;
            &:before
            {
                position: relative;
                left: -5px;

                content: '-';
            }
        }
    }
    ol
    {
        margin: 0;
        padding-top: 0;
        padding-left: 30px;
        li
        {
            font-size: .7rem;

            margin-top: 0;
            margin-bottom: .2rem;
            margin-left: 10px;
            padding: 0;

            text-indent: -5px;
        }
    }
}
form.searchBox
{
    position: relative;

    width: 180px;
    height: 20px;

    border: 1px solid #4682be;
}
input[type=search]
{
    position: absolute;
    top: 0;
    left: 0;

    width: 160px;
    height: 18px;

    text-align: left;

    border: none;
    outline: none;
}
input.searchButton
{
    position: absolute;
    top: 0;
    left: 160px;

    height: 18px;
}
#content
{
    font-size: .8rem;
    line-height: 1.3rem;

    margin-right: 20px;
    margin-left: 20px;
    padding-top: 10px;
    padding-bottom: 150px;

    counter-reset: section;
    pre
    {
        line-height: 1.2;

        margin-right: auto;
        margin-left: auto;
        padding-top: 10px;
        padding-bottom: 10px;
        padding-left: .7rem;

        color: @color_3;
        border-radius: 5px;
        background: #111;
    }
    code
    {
        font-family: @font_family_2;
        font-size: .7rem;
        font-weight: normal;

        color: @color_4;
        border-radius: 2px;
        background: #111;
    }
    p
    {
        code
        {
            padding-right: 3px;
            padding-left: 3px;

            color: @color_5;
            background: #f9f2f4;
        }
    }
    li
    {
         > code
        {
            padding-right: 3px;
            padding-left: 3px;

            color: @color_5;
            background: #f9f2f4;
        }
    }
    h2
    {
        font-size: 18px;

        margin-top: 50px;
        margin-bottom: 0;
        padding-top: 20px;
        padding-bottom: 0;

        counter-increment: section;
        text-align: left;

        border-top: 2px solid #666;
         > code
        {
            padding-right: 3px;
            padding-left: 3px;

            color: @color_5;
            background: #f9f2f4;
        }
        &:before
        {
            content: counter(section) '. ';
        }
        &:hover
        {
            color: @color_6;
        }
    }
    h3
    {
        margin-top: 50px;
        margin-bottom: 0;
        padding-top: 20px;
        padding-bottom: 0;

        text-align: left;

        border-top: 1px dotted #777;
         > code
        {
            padding-right: 3px;
            padding-left: 3px;

            color: @color_5;
            background: #f9f2f4;
        }
        &:hover
        {
            color: @color_6;
        }
    }
    img
    {
        display: block;

        max-width: 90%;
        margin-top: 40px;
        margin-right: auto;
        margin-bottom: 40px;
        margin-left: auto;

        border-radius: 5px;
    }
    ul
    {
        display: block;

        padding-top: .5rem;
        padding-bottom: .5rem;

        list-style-type: none;
        li
        {
            font-size: .8rem;

            text-indent: -5px;
            &:before
            {
                position: relative;
                left: -5px;

                content: '-';
            }
        }
        li.link
        {
            font-size: .7rem;
            font-weight: bold;

            cursor: pointer;
            text-decoration: none;

            color: @color_7;
        }
    }
    ol
    {
        display: block;

        padding-top: .5rem;
        padding-bottom: .5rem;
        li
        {
            font-size: .8rem;

            text-indent: -5px;
        }
        li.link
        {
            font-size: .7rem;
            font-weight: bold;

            cursor: pointer;
            text-decoration: none;

            color: @color_7;
        }
    }
    a
    {
        &:link
        {
            text-decoration: none;

            color: @color_2;
        }
        &:visited
        {
            text-decoration: none;

            color: @color_2;
        }
    }
    .content-toc
    {
        border-radius: 5px;
        background: #bdc3c7;
    }
    table
    {
        display: block;
        overflow: auto;

        width: 100%;

        word-break: normal;
        word-break: keep-all;
        th
        {
            font-weight: bold;

            padding: 6px 13px;

            border: 1px solid #ddd;
        }
        td
        {
            padding: 6px 13px;

            border: 1px solid #ddd;
        }
        tr
        {
            border-top: 1px solid #ccc;
            background-color: @background_color_1;
            &:nth-child(2n)
            {
                background-color: @background_color_2;
            }
        }
    }
}
#back_to_top
{
    font-size: 10px;
    line-height: 20px;

    position: fixed;
    top: 20px;

    display: none;

    width: 70px;
    height: 20px;
    margin-top: 0;
    margin-left: 930px;

    text-align: center;

    color: @color_3;
    border-radius: 5px;
    background-color: @background_color_3;
    &:hover
    {
        cursor: pointer;

        background-color: @background_color_4;
    }
}
#edit
{
    font-size: 10px;
    line-height: 17px;

    position: fixed;
    top: 45px;

    display: none;

    width: 70px;
    height: 17px;
    margin-top: 0;
    margin-left: 930px;

    text-align: center;

    color: @color_3;
    border-radius: 5px;
    background-color: @background_color_3;
    &:hover
    {
        cursor: pointer;

        background-color: @background_color_4;
    }
}
#loading
{
    font-size: 14px;

    position: fixed;
    top: 0;

    display: none;

    height: 17px;
}
#error
{
    font-size: 14px;

    position: fixed;
    top: 0;

    display: none;

    height: 17px;
}
#flip
{
    font-size: 10px;

    position: fixed;
    bottom: 10px;

    width: 100%;
    height: 20px;

    text-align: center;
}
#pageup
{
    line-height: 20px;

    display: inline-block;

    width: 70px;
    height: 20px;
    margin-right: 5px;

    text-align: center;

    color: @color_3;
    border-radius: 5px;
    background-color: @background_color_3;
    &:hover
    {
        cursor: pointer;

        background-color: @background_color_4;
    }
}
#pagedown
{
    line-height: 20px;

    display: inline-block;

    width: 70px;
    height: 20px;
    margin-left: 5px;

    text-align: center;

    color: @color_3;
    border-radius: 5px;
    background-color: @background_color_3;
    &:hover
    {
        cursor: pointer;

        background-color: @background_color_4;
    }
}
.progress-indicator-2
{
    position: fixed;
    top: 0;
    left: 0;

    height: 3px;

    background-color: @background_color_5;
}
code[class*='language-']
{
    font-family: @font_family_3;

    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    -moz-tab-size: 4;
      -o-tab-size: 4;
         tab-size: 4;
    -webkit-hyphens: none;
       -moz-hyphens: none;
            hyphens: none;

    color: @color_4;
    text-shadow: 0 1px rgba(0,0,0,.3);

    direction: ltr;
        -ms-hyphens: none;
}
pre[class*='language-']
{
    font-family: @font_family_3;

    overflow: auto;

    margin: .5em 0;
    padding: 1em;

    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    -moz-tab-size: 4;
      -o-tab-size: 4;
         tab-size: 4;
    -webkit-hyphens: none;
       -moz-hyphens: none;
            hyphens: none;

    color: @color_4;
    border-radius: .3em;
    background: #272822;
    text-shadow: 0 1px rgba(0,0,0,.3);

    direction: ltr;
        -ms-hyphens: none;
}
&:not(pre)
{
     > code[class*='language-']
    {
        padding: .1em;

        border-radius: .3em;
        background: #272822;
    }
}
.token.comment
{
    color: @color_8;
}
.token.prolog
{
    color: @color_8;
}
.token.doctype
{
    color: @color_8;
}
.token.cdata
{
    color: @color_8;
}
.token.punctuation
{
    color: @color_9;
}
.namespace
{
    opacity: .7;
}
.token.property
{
    color: @color_10;
}
.token.tag
{
    color: @color_10;
}
.token.constant
{
    color: @color_10;
}
.token.symbol
{
    color: @color_10;
}
.token.boolean
{
    color: @color_11;
}
.token.number
{
    color: @color_11;
}
.token.selector
{
    color: @color_4;
}
.token.attr-name
{
    color: @color_4;
}
.token.string
{
    color: @color_4;
}
.token.builtin
{
    color: @color_4;
}
.token.operator
{
    color: @color_10;
}
.token.entity
{
    cursor: help;

    color: @color_10;
}
.token.url
{
    color: @color_10;
}
.language-css
{
    .token.string
    {
        color: @color_10;
    }
}
.style
{
    .token.string
    {
        color: @color_10;
    }
}
.token.variable
{
    color: @color_10;
}
.token.atrule
{
    color: @color_12;
}
.token.attr-value
{
    color: @color_12;
}
.token.keyword
{
    color: @color_13;
}
.token.regex
{
    color: @color_14;
}
.token.important
{
    font-weight: bold;

    color: @color_14;
}
@media (min-width: 40rem)
{
    html
    {
        font-size: 112%;
    }
    #sidebar
    {
        position: fixed;

        overflow: -moz-scrollbars-vertical;
        overflow-y: auto;

        width: 280px;
        height: 100%;
        margin-right: 20px;
        padding-top: 0;
        padding-bottom: 0;
    }
    #content
    {
        width: 80%;
        margin-right: 0;
        margin-left: 0;
        padding-left: 330px;
    }
    #loading
    {
        font-size: 14px;

        position: fixed;
        top: 45%;

        display: none;

        height: 17px;
        margin-left: 560px;
    }
    #error
    {
        font-size: 14px;

        position: fixed;
        top: 45%;

        display: none;

        height: 17px;
        margin-left: 560px;
    }
    #flip
    {
        width: 150px;
        margin-left: 545px;
    }
}
@media (min-width: 64rem)
{
    html
    {
        font-size: 120%;
    }
}
